@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;700&display=swap);
.container{display:grid;grid-template-columns:minmax(auto, 1024px);justify-content:center}.f-center{display:flex;justify-content:center;align-items:center}.box-shadow{box-shadow:1px 2px 5px rgba(0,0,0,0.3)}.text-center{text-align:center}.bg-black{background-color:#000}@media only screen and (max-width: 1023px){.container{grid-template-columns:minmax(auto, 768px);padding:0 2rem}}.scale-in-center{-webkit-animation:scale-in-center 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;animation:scale-in-center 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both}@-webkit-keyframes scale-in-center{0%{-webkit-transform:scale(0);transform:scale(0);opacity:1}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}@keyframes scale-in-center{0%{-webkit-transform:scale(0);transform:scale(0);opacity:1}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}.fade-in{-webkit-animation:fade-in-keyframes 1s ease;animation:fade-in-keyframes 1s ease}@-webkit-keyframes fade-in-keyframes{from{-webkit-filter:blur(5px);filter:blur(5px);opacity:0}to{-webkit-filter:blur(0);filter:blur(0);opacity:1}}@keyframes fade-in-keyframes{from{-webkit-filter:blur(5px);filter:blur(5px);opacity:0}to{-webkit-filter:blur(0);filter:blur(0);opacity:1}}.pill{font-size:0.7rem;padding:0rem 0.5rem;border-radius:30px;color:#000;font-weight:bold;margin:0 0.2rem}.pill__skyblue{background-color:#61dafb}.pill__orange{background:#f7df1e}.pill__blue{background:#00298a;color:#fff}.pill__pink{background:#d75893;color:#fff}.pill__default{background-color:#fff}.btn{padding:0.5rem 1.5rem;border-radius:30px;border:none;outline:none;cursor:pointer;transition:0.3s;text-decoration:none}.btn__blue{background-color:#001a57;color:#fff}.btn__blue:hover{background-color:#00298a}body,button{margin:0;font-family:"Quicksand", sans-serif}

.hero_position,.HeroSurface,.HeroSurface .surface,.HeroSurface .fog{position:absolute;height:40%;bottom:0;left:0;right:0;z-index:1}.HeroSurface .surface{background-image:url(../../static/media/hero_footer_2.e74c5f00.png);background-size:100%;background-repeat:no-repeat;background-position-y:100%;height:100%}.HeroSurface .fog{height:100%;width:100%;transition:0.3s;z-index:4}@-webkit-keyframes cloud_moved{0%{background-position-x:-100%}100%{background-position-x:100%}}@keyframes cloud_moved{0%{background-position-x:-100%}100%{background-position-x:100%}}

.HeroCanvas{position:absolute;min-height:inherit;width:100%;overflow:hidden;z-index:1}

.container{display:grid;grid-template-columns:minmax(auto, 1024px);justify-content:center}.f-center,.Hero{display:flex;justify-content:center;align-items:center}.box-shadow{box-shadow:1px 2px 5px rgba(0,0,0,0.3)}.text-center{text-align:center}.bg-black{background-color:#000}@media only screen and (max-width: 1023px){.container{grid-template-columns:minmax(auto, 768px);padding:0 2rem}}.Hero{min-height:100vh;color:#fff;flex-direction:column;position:relative}.Hero h1{font-size:4rem;font-weight:700;z-index:2;transition:0.3s}.Hero .List{list-style:none;margin:0;padding:0;z-index:2}.Hero .List li{margin:0;display:inline-block;transition:0.3s}.Hero .List li:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.Hero .List li:nth-child(2){margin-left:3rem}@media only screen and (max-width: 768px){.Hero h1{font-size:3rem}}

.Projects{padding:3.5rem 0 2rem 0;color:#fff}.Projects h2{font-size:2rem}

.Project{display:grid;grid-template-columns:repeat(2, 50%);grid-template-rows:280px;box-sizing:border-box;border-radius:30px;overflow:hidden;background-color:#241231;box-shadow:2px 3px 5px rgba(0,0,0,0.3);margin:1rem 0}.Project__text{padding:2rem 0 2rem 2rem;display:flex;flex-direction:column;justify-content:center}.Project__text h4{font-size:1.5rem;margin:0}.Project__description p{margin:1.5rem 0}.Project__img>figure{margin:0;position:relative;height:100%;overflow:hidden}.Project__img img{position:absolute;top:35px;left:25px;width:100%;bottom:0;border-radius:30px;transition:0.3s}.Project__img img:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}

.container{display:grid;grid-template-columns:minmax(auto, 1024px);justify-content:center}.f-center,.Menu_icon{display:flex;justify-content:center;align-items:center}.box-shadow{box-shadow:1px 2px 5px rgba(0,0,0,0.3)}.text-center{text-align:center}.bg-black{background-color:#000}@media only screen and (max-width: 1023px){.container{grid-template-columns:minmax(auto, 768px);padding:0 2rem}}.Header{color:#fff;width:100%;height:80px;position:fixed;z-index:3;box-sizing:border-box;transition:0.3s}.Header .container{height:100%}.Header_container{display:flex;justify-content:space-between;align-items:center}.Header_container .Logo{margin:0}.Header_container .Logo img{height:70px}.Header.bg-black{box-shadow:0px 2px 5px rgba(0,0,0,0.6)}.Menu{position:relative}.Menu_icon{background-color:#fff;color:#000;width:50px;height:50px;border-radius:50%;cursor:pointer;transition:0.3s;position:relative}.Menu_icon:hover{-webkit-transform:scale(0.9);transform:scale(0.9)}.Menu_nav{position:absolute;right:10px;top:80px;background-color:#fff;padding:0 1rem;border-radius:10px;box-sizing:border-box;min-width:250px}.Menu_nav ul{list-style:none;padding:0}.Menu_nav li{margin:0}.Menu_nav a{padding:0.5rem;border-radius:10px;text-decoration:none;color:rgba(0,0,0,0.8);display:grid;grid-template-columns:20% 1fr;align-items:center;transition:0.3s}.Menu_nav a:hover{background-color:#241231;color:#fff}

.Footer{background-color:#000;color:#fff;padding:2rem 0}

.Home{position:relative;background:#211030;background:linear-gradient(315deg, #211030 0%, #000 100%)}

